<template>
  <div class="formConfig">
    <el-form label-position="top" v-if="record.key">
      <el-form-item label="">
        <el-input
          size="small"
          v-model="record.model"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(0, 'label')">
        <el-input
          size="small"
          v-model="record.label"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'isRange')">
        <el-switch v-model="record.options.isRange" @change="handleReset">
        </el-switch>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'placeholder')">
        <div v-if="record.options.isRange">
          <el-input
            size="small"
            v-model="record.options.startPlaceholder"
            placeholder=""
          ></el-input>
          <el-input
            size="small"
            v-model="record.options.endPlaceholder"
            placeholder=""
          ></el-input>
        </div>
        <el-input
          v-else
          size="small"
          v-model="record.options.placeholder"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'type')">
        <el-radio-group v-model="record.options.type" size="small">
          <el-radio label="text"></el-radio>
          <el-radio label="password"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'options')">
        <select-config
          :record="record"
          @handleDelete="(obj) => $emit('handleDelete', obj)"
          @handleAdd="(obj) => $emit('handleAdd', obj)"
        ></select-config>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'defaultValue')">
        <!--  -->
        <el-radio-group
          v-if="record.type === 'radio' || record.type === 'select'"
          v-model="record.options.defaultValue"
        >
          <div v-if="!record.options.dynamic">
            <el-radio
              v-for="option in record.options.options"
              :key="option.value"
              :label="option.value"
              >{{ option.label }}</el-radio
            >
          </div>
          <div v-else>
            <!--  -->
            <el-radio
              v-for="option in dynamicData[record.options.dynamicKey]"
              :key="option.value"
              :label="option.value"
              >{{ option.label }}</el-radio
            >
          </div>
        </el-radio-group>
        <!--  -->
        <el-checkbox-group
          v-else-if="record.type === 'checkbox'"
          v-model="record.options.defaultValue"
        >
          <div v-if="!record.options.dynamic">
            <el-checkbox
              v-for="option in record.options.options"
              :key="option.value"
              :label="option.value"
              >{{ option.label }}</el-checkbox
            >
          </div>
          <div v-else>
            <!--  -->
            <el-checkbox
              v-for="option in dynamicData[record.options.dynamicKey]"
              :key="option.value"
              :label="option.value"
              >{{ option.label }}</el-checkbox
            >
          </div>
        </el-checkbox-group>
        <!--  -->
        <el-time-picker
          key="1"
          v-else-if="record.type === 'timePicker' && !record.options.isRange"
          v-model="record.options.defaultValue"
          placeholder=""
          size="small"
        >
        </el-time-picker>
        <el-time-picker
          key="2"
          v-else-if="record.type === 'timePicker' && record.options.isRange"
          v-model="record.options.defaultValue"
          is-range
          range-separator="-"
          placeholder=""
          start-placeholder=""
          end-placeholder=""
          size="small"
          :value-format="record.options.valueFormat"
        >
        </el-time-picker>

        <el-date-picker
          key="3"
          v-else-if="
            record.type === 'datePicker' &&
            !['monthrange', 'daterange', 'datetimerange'].includes(
              record.options.dateType
            )
          "
          v-model="record.options.defaultValue"
          placeholder=""
          size="small"
          :value-format="record.options.valueFormat"
          :type="record.options.dateType"
        >
        </el-date-picker>

        <el-date-picker
          key="4"
          v-else-if="
            record.type === 'datePicker' &&
            ['monthrange', 'daterange', 'datetimerange'].includes(
              record.options.dateType
            )
          "
          v-model="record.options.defaultValue"
          range-separator="-"
          placeholder=""
          start-placeholder=""
          end-placeholder=""
          size="small"
          :value-format="record.options.valueFormat"
          :type="record.options.dateType"
        >
        </el-date-picker>

        <el-rate
          v-else-if="record.type === 'rate'"
          v-model="record.options.defaultValue"
          :allow-half="record.options.allowHalf"
          :max="record.options.max"
        ></el-rate>

        <el-switch
          v-else-if="record.type === 'switch'"
          v-model="record.options.defaultValue"
        >
        </el-switch>

        <el-slider
          v-else-if="record.type === 'slider'"
          v-model="record.options.defaultValue"
          size="small"
          :max="record.options.max"
          :min="record.options.min"
          :step="record.options.step"
        ></el-slider>

        <el-input
          v-else
          size="small"
          v-model="record.options.defaultValue"
          placeholder=""
        ></el-input>
        <el-button
          v-if="
            record.type === 'radio' ||
            record.type === 'select' ||
            record.type === 'checkbox'
          "
          type="primary"
          icon="el-icon-refresh"
          circle
          size="small"
          @click="handleReset"
          title=""
        ></el-button>
      </el-form-item>

      <!--  start -->
      <el-form-item label="" v-if="isShowFunc(1, 'lisTtype')">
        <el-radio-group v-model="record.options.lisTtype" size="small">
          <el-radio label="text"></el-radio>
          <el-radio label="picture"></el-radio>
          <el-radio label="picture-card"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'limit')">
        <el-input-number
          size="small"
          v-model="record.options.limit"
          placeholder=""
          :min="1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'fileName')">
        <el-input
          size="small"
          v-model="record.options.fileName"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'action')">
        <el-input
          size="small"
          v-model="record.options.action"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="JSON" v-if="isShowFunc(1, 'data')">
        <el-input
          size="small"
          v-model="record.options.data"
          type="textarea"
          :rows="3"
          placeholder="JSON"
        ></el-input>
      </el-form-item>
      <!--  end -->

      <!--  start -->
      <el-form-item label="" v-if="isShowFunc(1, 'btnType')">
        <el-radio-group v-model="record.options.btnType" size="small">
          <el-radio label="primary"></el-radio>
          <el-radio label="success"></el-radio>
          <el-radio label="warning"></el-radio>
          <el-radio label="danger"></el-radio>
          <el-radio label="info"></el-radio>
          <el-radio label="text"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'plain')">
        <el-switch v-model="record.options.plain" size="small"></el-switch>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'round')">
        <el-switch v-model="record.options.round" size="small"></el-switch>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'nativeType')">
        <el-radio-group v-model="record.options.nativeType" size="small">
          <el-radio label="submit"></el-radio>
          <el-radio label="reset"></el-radio>
          <el-radio label="dynamic"></el-radio>
        </el-radio-group>
        <el-input
          v-show="record.options.nativeType === 'dynamic'"
          v-model.number="record.options.dynamicFun"
          size="small"
          placeholder=""
        ></el-input>
      </el-form-item>
      <!--  end-->

      <!--  start -->
      <el-form-item label="" v-if="isShowFunc(1, 'alertType')">
        <el-radio-group v-model="record.options.alertType" size="small">
          <el-radio label="success"></el-radio>
          <el-radio label="warning"></el-radio>
          <el-radio label="error"></el-radio>
          <el-radio label="info"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'effect')">
        <el-radio-group v-model="record.options.effect" size="small">
          <el-radio label="light">light</el-radio>
          <el-radio label="dark">dark</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'description')">
        <el-input
          size="small"
          v-model="record.options.description"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'closable')">
        <el-switch v-model="record.options.closable" size="small"></el-switch>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'showIcon')">
        <el-switch v-model="record.options.showIcon" size="small"></el-switch>
      </el-form-item>

      <!--  end -->

      <el-form-item label="" v-if="isShowFunc(1, 'hidden')">
        <el-checkbox v-model="record.options.hidden"></el-checkbox>
        <el-checkbox
          v-model="record.options.disabled"
          v-if="isShowFunc(1, 'disabled')"
          ></el-checkbox
        >
        <el-checkbox
          v-model="record.options.clearable"
          v-if="isShowFunc(1, 'clearable')"
          ></el-checkbox
        >
        <el-checkbox
          v-model="record.options.readonly"
          v-if="isShowFunc(1, 'readonly')"
          ></el-checkbox
        >
        <el-checkbox
          v-model="record.options.editable"
          v-if="isShowFunc(1, 'editable')"
          ></el-checkbox
        >
        <el-checkbox
          v-model="record.options.multiple"
          v-if="isShowFunc(1, 'multiple')"
          ></el-checkbox
        >
        <el-checkbox v-model="record.options.drag" v-if="isShowFunc(1, 'drag')"
          ></el-checkbox
        >
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'filterable')">
        <el-switch v-model="record.options.filterable"> </el-switch>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'valueFormat')">
        <el-input
          size="small"
          v-model="record.options.valueFormat"
          placeholder="HH:mm:ss"
          @input="handleReset"
        ></el-input>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'dateType')">
        <el-select
          v-model="record.options.dateType"
          size="small"
          @change="handleReset"
        >
          <el-option
            v-for="option in dateType"
            :key="option.value"
            :label="option.label"
            :value="option.value"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'step')">
        <el-input-number
          v-model="record.options.step"
          size="small"
          :min="1"
        ></el-input-number>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'startMax')">
        <el-input-number
          v-model="record.options.startMax"
          size="small"
          :max="10"
          :min="1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'max')">
        <el-input-number
          v-model="record.options.max"
          size="small"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'min')">
        <el-input-number
          v-model="record.options.min"
          size="small"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'showInput')">
        <el-switch v-model="record.options.showInput" size="small"></el-switch>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'allowHalf')">
        <el-switch v-model="record.options.allowHalf" size="small"></el-switch>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'rows')">
        <el-input
          size="small"
          v-model.number="record.options.rows"
          placeholder=""
        ></el-input>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(1, 'width')">
        <el-input
          size="small"
          v-model="record.options.width"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'labelWidth')">
        <el-checkbox v-model="record.options.labelWidth.isOpen"
          ></el-checkbox
        >
        <el-slider
          :disabled="!record.options.labelWidth.isOpen"
          v-model="record.options.labelWidth.width"
          show-input
          :max="500"
        >
        </el-slider>
      </el-form-item>

      <el-form-item label="" v-if="isShowFunc(0, 'rules')">
        <el-checkbox v-model="record.rules[0].required"></el-checkbox>
        <el-input
          v-if="record.rules[0].required"
          size="small"
          v-model="record.rules[0].message"
          placeholder=""
        ></el-input>
        <div class="flex-sb" v-if="record.rules[1]">
          <el-checkbox v-model="record.rules[1].userFlag"></el-checkbox>
          <el-select
            v-model="record.rules[1].type"
            placeholder=""
            size="small"
            :disabled="!record.rules[1].userFlag"
          >
            <el-option key="string" label="" value="string"> </el-option>
            <el-option key="number" label="" value="number"> </el-option>
            <el-option key="integer" label="" value="integer"> </el-option>
            <el-option key="float" label="" value="float"> </el-option>
            <el-option key="url" label="URL" value="url"> </el-option>
            <el-option key="email" label="" value="email"> </el-option>
            <el-option key="phone" label="" value="phone"> </el-option>
          </el-select>
        </div>
        <el-input
          v-if="record.rules[1] && record.rules[1].userFlag"
          size="small"
          v-model="record.rules[1].message"
          placeholder=""
        ></el-input>
      </el-form-item>

      <!--  start-->
      <el-form-item label="" v-if="isShowFunc(1, 'fontSize')">
        <el-input-number
          v-model="record.options.fontSize"
          size="small"
          :min="12"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'color')">
        <el-color-picker
          v-model="record.options.color"
          size="small"
        ></el-color-picker>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'fontWeight')">
        <el-radio-group v-model="record.options.fontWeight" size="small">
          <el-radio-button label="lighter"></el-radio-button>
          <el-radio-button label="normal"></el-radio-button>
          <el-radio-button label="bold"></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'align')">
        <el-radio-group v-model="record.options.align" size="small">
          <el-radio-button label="left"></el-radio-button>
          <el-radio-button label="center"></el-radio-button>
          <el-radio-button label="right"></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'marginLeft')">
        <el-input-number
          v-model="record.options.marginLeft"
          size="small"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="" v-if="isShowFunc(1, 'marginRight')">
        <el-input-number
          v-model="record.options.marginRight"
          size="small"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <!--  end-->

      <!--  start -->
      <template v-if="record.type === 'dynamicTab'">
        <el-form-item label="">
          <el-input
            size="small"
            v-model.number="record.options.scrollY"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch
            v-model="record.options.hideSequence"
            size="small"
          ></el-switch>
        </el-form-item>
      </template>
      <!--  end -->

      <!--  start -->
      <template v-if="record.type === 'divider'">
        <el-form-item label="">
          <el-radio-group v-model="record.options.contentPosition" size="small">
            <el-radio-button label="left"></el-radio-button>
            <el-radio-button label="center"></el-radio-button>
            <el-radio-button label="right"></el-radio-button>
          </el-radio-group>
        </el-form-item>
      </template>
      <!--  end -->

      <!--  start -->
      <template v-if="record.type === 'card'">
        <el-form-item label="">
          <el-radio-group v-model="record.options.shadow" size="small">
            <el-radio-button label="always"></el-radio-button>
            <el-radio-button label="hover"></el-radio-button>
            <el-radio-button label="never"></el-radio-button>
          </el-radio-group>
        </el-form-item>
      </template>
      <!--  end -->

      <!-- start -->
      <template v-if="record.type === 'tabs'">
        <el-form-item label="">
          <el-radio-group v-model="record.options.tabsType" size="small">
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label="border-card"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="record.options.tabPosition" size="small">
            <el-radio-button label="top"></el-radio-button>
            <el-radio-button label="left"></el-radio-button>
            <el-radio-button label="bottom"></el-radio-button>
            <el-radio-button label="right"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <select-config-tabs
            :record="record"
            @handleDelete="(obj) => $emit('handleDelete', obj)"
            @handleAdd="(obj) => $emit('handleAdd', obj)"
          ></select-config-tabs>
        </el-form-item>
      </template>

      <!-- end -->

      <!--  start -->
      <template v-if="record.type === 'grid'">
        <el-form-item label="">
          <el-input
            size="small"
            v-model="record.options.gutter"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <div v-for="(column, index) in record.columns" :key="index">
            <el-row>
              <el-col :span="21">
                <el-input
                  size="small"
                  v-model.number="column.span"
                  placeholder=""
                ></el-input>
              </el-col>
              <el-col :span="3">
                <i
                  class="el-icon-delete-solid icon-delete-solid"
                  @click="$emit('handleDelete', { type: 'grid', index: index })"
                ></i>
              </el-col>
            </el-row>
          </div>
          <el-button
            type="primary"
            icon="el-icon-plus"
            circle
            size="small"
            @click="$emit('handleAdd', { type: 'grid' })"
          ></el-button>
        </el-form-item>
      </template>
      <!--  end -->

      <!--  start -->
      <template v-if="record.type === 'tableLayout'">
        <el-form-item label="">
          <el-slider v-model="record.options.borderWidth" :max="5" :min="0">
          </el-slider>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="record.options.paddingStyle" size="mini">
            <el-radio-button label="12px"></el-radio-button>
            <el-radio-button label="8px 12px"></el-radio-button>
            <el-radio-button label="3px 6px"></el-radio-button>
            <el-radio-button label="0"></el-radio-button>
          </el-radio-group>
        </el-form-item>
      </template>
      <!--  end -->
    </el-form>
    <div v-else>
      <el-empty description="" :image-size="100"></el-empty>
    </div>
  </div>
</template>

<script>
import SelectConfig from "./SelectConfig.vue";
import SelectConfigTabs from "./SelectConfigTabs.vue";
export default {
  data() {
    return {
      dateType: [
        { label: "", value: "year" },
        { label: "", value: "month" },
        { label: "", value: "date" },
        { label: "", value: "dates" },
        { label: "", value: "week" },
        { label: "", value: "datetime" },
        { label: "", value: "monthrange" },
        { label: "", value: "daterange" },
        { label: "", value: "datetimerange" },
      ],
    };
  },
  props: {
    record: {
      type: Object,
      required: true,
    },
    dynamicData: {
      type: Object,
      default: () => ({}),
    },
  },
  components: {
    SelectConfig,
    SelectConfigTabs,
  },
  created() {},
  methods: {
    isShowFunc(flag, target) {
      // 
      let type = "";
      if (flag === 0) {
        type = typeof this.record[target];
      } else if (flag === 1) {
        type = typeof this.record.options[target];
      }
      return type !== "undefined";
    },
    handleReset() {
      this.$emit("handleResetSelectValue", { type: this.record.type });
    },
  },
  computed: {
    // 
    // isHiddenValue() {
    //   console.log(['radio'].includes(this.record.type));
    //   return !(['radio'].includes(this.record.type))
    // }
  },
};
</script>

<style scoped lang="scss">
</style>